import React from "react"
import styled from "styled-components"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import Layout from "globalLayoutsPortfolioCategorySinglePage"

const PageStyle = styled.div`
  margin-top: 48px;
`

export default function PortfolioCategorySinglePage(props) {
  return (
    <>
      <GatsbySeo
        title={
          props.pageContext.category !== undefined &&
          props.pageContext.category.title
        }
        description={
          props.pageContext.category !== undefined &&
          props.pageContext.category.description
        }
        canonical={`${props.location.href}`}
        openGraph={{
          url: props.location.href,
          title:
            props.pageContext.category !== undefined &&
            props.pageContext.category.title,
          description:
            props.pageContext.category !== undefined &&
            props.pageContext.category.description,
        }}
      />
      <Layout>
        <PageStyle
          as="article"
          className="container-fluid"
          style={{ maxWidth: "992px" }}
        >
          <h1>
            {props.pageContext.category !== undefined &&
              props.pageContext.category.title}
          </h1>
        </PageStyle>
      </Layout>
    </>
  )
}
